.magazine {
  margin: 22.25% auto!important;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@media (max-width: 500px) {
  .magazine {
    margin: 60% auto!important;
  }
}

.magazine .page {
  height: 100%;
}


.magazine .page img {
  max-width: 100%;
  height: 100%;
}

html, body {
    margin: 0;
    height: 100%;
}

body {
    background-color: #333;
}

body.hide-overflow {
    overflow: hidden;
}

/* helpers */

.t {
    display: table;
    width: 100%;
    height: 100%;
}

.tc {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}

.rel {
    position: relative;
}

img.navigation {
  font-family: 'Montserrat';
  position: fixed;
  z-index: 999999999999;
  top: 50%;
  border-radius: 50%;
  background: "./SVG/circle.svg";
  width: 50px;
  background: #fff;

}

img.navigation:hover {
  cursor: pointer;
}

img.next {
  right: 2%;

}

img.prev {
  left: 2%;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}


